import Vue from 'vue'
import VueRouter from 'vue-router'

import Music from '../views/Music.vue'
import Dancing from '../views/Dancing.vue'
import Visual from '../views/Visual.vue'

//  TODO: can't jump into other pages other than home



Vue.use(VueRouter)

const routes = [
  {
    path: '/Music',
    component: Music
  },
  {
    path: '/dancing',
    component: Dancing
  },
  {
    path: '/Visual',
    component: Visual
  },
  
]

const router = new VueRouter({
  routes,
  mode: 'history',
})

export default router